/* Media Query Checker - START
*********************************/
#mediaquery {
  position: absolute;
  top: -50px;
  -webkit-transition: width .001s;
  -moz-transition: width .001s;
  -ms-transition: width .001s;
  -o-transition: width .001s;
  transition: width .001s;
  visibility: hidden; }

@media (min-width: 20em) {
  #mediaquery {
    width: 50px; }
  #mediaquery:after {
    content: "mobile"; } }

@media (min-width: 64em) {
  /* Changed to fix issue with tablets, original value 768 */
  #mediaquery {
    width: 100px; }
  #mediaquery:after {
    content: "tablet"; } }

@media (min-width: 72em) {
  /* Changed to fix issue with tablets, original value 1024 */
  #mediaquery {
    width: 150px; }
  #mediaquery:after {
    content: "desktop"; } }

